import { AlternatedatetimePage } from './../alternatedatetime/alternatedatetime.page';
/* eslint-disable @typescript-eslint/naming-convention */
import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { IonRouterOutlet, ModalController } from '@ionic/angular';
import { Router, ActivatedRoute } from '@angular/router';
import { EventsService } from 'src/app/services/events.service';
import { LocalService } from 'src/app/services/local.service';
import { RestService } from 'src/app/services/rest.service';
import { UtilityService } from 'src/app/services/utility.service';

@Component({
  selector: 'app-jobreceived',
  templateUrl: './jobreceived.page.html',
  styleUrls: ['./jobreceived.page.scss'],
})
export class JobreceivedPage implements OnInit {
  id: any = null;
  loggedCleaner: any;
  job: any = {};
  off: any;
  currency: any;
  settings: any = {};
  canGoBack = false;
  constructor(
    public localService: LocalService,
    public restService: RestService,
    public utilityService: UtilityService,
    public eventsService: EventsService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private location: Location,
    private modalCtrl: ModalController,
    private routerOutlet: IonRouterOutlet
  ) {
    this.loggedCleaner = this.localService.getCleaner();
    this.settings = this.localService.getSettings();
    this.currency = this.settings.currency_symbol;
    this.activatedRoute.queryParams.subscribe((params) => {
      if (params.id) {
        this.id = params.id;
        this.utilityService.loadingOpen('Loading...');
        // get job details
        this.restService.getData('jobdetails/' + this.id).subscribe(
          (data) => {
            this.utilityService.loadingClose();
            // get order data
            this.job = data;

            // trim whitespace from address
            this.job.address = this.job.address.trim();

            // calculate the payable price as per commission
            if (this.settings.commission_type === 'percentage') {
              // percentage
              this.job.total =
                this.job.total -
                (this.job.total * this.settings.commission) / 100;
            } else {
              // flat
              this.job.total = this.job.total - this.settings.commission;
            }
          },
          (err) => {
            // error
          }
        );
      } else {
        this.location.back();
      }
    });
  }

  ngOnInit() {
    this.canGoBack = this.routerOutlet && this.routerOutlet.canGoBack();
  }
  reject() {
    this.utilityService.loadingOpen('Processing...');
    this.restService
      .postData('rejectjob', {
        job_id: this.id,
        cleaner_id: this.loggedCleaner.id,
      })
      .subscribe(
        (data) => {
          this.utilityService.loadingClose();
          this.router.navigateByUrl('/cleanernotifications');
        },
        (err) => {
          this.utilityService.loadingClose();
          this.router.navigateByUrl('/cleanernotifications');
        }
      );
  }

  jobStatusChange(id) {
    console.log(id);
    this.utilityService.loadingOpen('Processing...');
    this.restService
      .postData('acceptjob', {
        job_id: this.id,
        cleaner_id: this.loggedCleaner.id,
      })
      .subscribe(
        (data) => {
          this.job.status = data.status;
          this.utilityService.loadingClose();
          this.router.navigateByUrl('/cleanerhome');
        },
        (err) => {
          this.utilityService.loadingClose();
          this.utilityService.openAlerts('Error!', err.error.error, 'ok');
        }
      );
  }

  getFrequency(name) {
    return this.utilityService.frequencies[name];
  }

  async alternate() {
    const modal = await this.modalCtrl.create({
      component: AlternatedatetimePage,
      componentProps: {
        job_id: this.job.id,
      },
      showBackdrop: true,
      backdropDismiss: true,
      cssClass: 'alternate-datetime',
    });
    return await modal.present();
  }
}
